import { Component, ViewChild, OnInit, ElementRef } from "@angular/core";
import { ProductService } from "../../providers/product.services";
import { environment } from "../../../environments/environment";
import { ActivatedRoute } from "@angular/router";
import { IonSearchbar } from "@ionic/angular";
import { ImageLoaderService, ImageAttribute } from "ionic-image-loader";

@Component({
  selector: "page-search",
  templateUrl: "search.html",
  styleUrls: ["./search.scss"],
})
export class SearchPage implements OnInit {
  public productList: any = [];
  public isSearchEnable: Boolean = false;
  public searchbar: any;

  slideOpts: any = {
    initialSlide: 0,
    speed: 400,
    spaceBetween: 10,
    pagination: true,
    slidesPerView: 2,
  };
  // categories = <any>[];
  // SubCategoryList: any = [];
  // recomented = <any>[];
  // popular = <any>[];
  searchWord: string;
  public baseUrl = environment.baseUrl + "/";
  public imageAttributes: ImageAttribute[] = [];
  public isDataLoaded: Boolean = false;
  public cartCount = 0;
  public addTocartCss: Boolean = false;

  constructor(
    private _ProductService: ProductService,
    private _Router: ActivatedRoute,
    private imageLoader: ImageLoaderService
  ) {}

  public from: any = "/home";

  ngOnInit() {
    this.imageAttributes.push({
      element: "style",
      value: "height: 73px; width: 111px;border-radius: 9%;",
    });
    console.log(this.searchbar, "-------------------");
    this.from = this._Router.snapshot.params.from;
    this.getProductList();
  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    console.log(document.getElementById("autofocus"), "box");
    this.searchbar = document.getElementById("autofocus");
    setTimeout(() => this.searchbar.setFocus(), 500);
  }

  getProductList() {
    this._ProductService
      .getProductList(localStorage.getItem("shopID"))
      .subscribe((data: any) => {
        this.productList = data;
        this.isDataLoaded = true;
        this.productList = this.productList.map((obj) => ({
          ...obj,
          QTY: 0,
        }));

        this.productList.forEach((product) => {
          product.price =
            this.convertIntoLakh(product.price)
        });

        console.log(this.productList);
      });
  }

 

  public searchproductList: any = [];



  clear() {
    this.getProductList();
  }

  getColor(index: number, rating) {
    if (this.isAboveRating(index, rating)) {
      return "grey";
    }

    return "gold";
  }

  isAboveRating(index: number, rating) {
    return index > rating;
  }



  search(query) {
    let shopId = localStorage.getItem("shopID");
    if (query.target.value.length >= 1) {
      this.isSearchEnable = true;
      this._ProductService
        .search(
          {
            search: query.target.value,
            filter: "Product",
          },
          shopId
        )
        .subscribe((data: any) => {
          console.log(data, "data");
          data.productList = data.productList.map((obj) => ({
            ...obj,
            QTY: 0,
            rating: 0,
          }));

          this.productList = data.productList;
          this.productList.forEach((product) => {
            product.price =
              this.convertIntoLakh(product.price)
          });
        });
    } else {
      this.clear();
    }

    if (query.target.value.length == 0) {
      this.isSearchEnable = false;
      // this.getAnalyticalData()
    }
  }

 

convertIntoLakh(value) {
 console.log(value,"value");
 let arr = value.split("-");
 console.log(arr,"arr");
 var v1:any =''
 var v2:any = ''
 if(arr.length == 2){
  v1 = Math.abs(arr[0]);
  v2 = Math.abs(arr[1]);
  v1 = (v1 / 100000).toFixed(2);
  v2 = (v2 / 100000).toFixed(2) ;
 }else{
  v1 = Math.abs(arr[0]);
  v1 = (v1 / 100000).toFixed(2);
  
 }

 let second = (v2)?" - "+v2:''
return v1+ second + ' Lakh';
}

 
}
